body {
    margin: 0px;
    padding: 0px;
}

#cv {
    width: 8.5in;
    height: 11in;
    padding: 0.5in;
    box-sizing: border-box;
}

#cv * {
    line-height: 1.2em;
    letter-spacing: 0.05rem;
}

#controls {
    width: calc(100vw - 8.5in);
    height: 100vh;
    padding: 64px;
    background-color: hsl(210, 100%, 80%);
    position: fixed;
    right: 0;
    top: 0;
    box-sizing: border-box;
    border-left: 32px solid hsl(70, 90%, 90%);
    text-align: center;
}


#fontSelector * {
    font-size: 32px;
    text-align: right;
}

#banner {
    font-family: 'Work Sans', sans-serif;
    margin-left: 96px;
    letter-spacing: 0rem;
    font-size: 64px;
    letter-spacing: 0.1rem;
    text-decoration: underline;
    margin-bottom: 128px;
    text-align: right;
}

#save {
    visibility: visible;
    font-size: 32px;
    margin-top: 64px;
}

#header {
    margin-bottom: 12px;
}

#contact {
    width: 90%;
    margin-bottom: 32px;
    border-bottom: 1px solid grey;
}

.line {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center
}

.line:hover {
    background-color: hsl(70, 90%, 90%);
}

:where(h1, h2, h3, h4, h5, p, li):hover {
    text-decoration: underline;
}

input[type=text], input[type=text]:focus-visible {
    padding: 0px;
    border: none;
    outline: none;
    color: dimgray;
    width: 100%;
}

.rowList {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
}

.rowList .line * {
    margin: 6px;
}

.rowList .line {
    width: fit-content;
}

h1, input.h1 {
    font-size: 36px;
    font-weight: bold;
    margin-top:6px;
    margin-bottom: 20px;
}

h2, input.h2 {
    font-size: 28px;
    margin-top:6px;
    margin-bottom: 16px;
}

h3, input.h3 {
    font-size: 20px;
    font-weight: normal;
    margin-top:6px;
    margin-bottom: 10px;
}

h4, input.h4 {
    font-size: 16px;
    font-weight: normal;
    margin-top: 6px;
    margin-bottom: 10px;
}

h5, input.h5 {
    font-size: 16px;
    font-weight: normal;
    margin-top: 6px;
    margin-bottom: 8px;
}

p, input.p {
    font-size: 20px;
    margin-top:6px;
    margin-bottom: 10px;
}

li, input.li {
    font-size: 16px;
    margin-top:6px;
    margin-bottom: 8px;
}

button {
    visibility: hidden;
    font-family: monospace;
}

.section:hover button {
    visibility: visible;
}